<template>
  <div> <!-- class="wrapper" -->

    <h2>component sandbox</h2>

    <!-- crosshair and outline rollover -->
    <h2>card with rollover</h2>
    <div class="row debug">
      <div class="span-4-md offset-1-md ratio-3-4 overflow-visible">
      </div>
    </div>
    

    <!-- card flip outer row/span -->
    <h2>Card with 3D flip effect and rollover</h2>
    <p>(click to rotate)</p>

    <div class="row debug">
      <div class="span-4-md offset-1-md ratio-3-4 overflow-visible">

        <!-- card flip -->
        <div class="flip-container">

          <input class="flip-indicator" type="checkbox"></input>

          <div class="flipper">

            <div class="front ind-2">

              <!-- inner front content -->
              <div class="front-inner">
                <div class="svg-icon-background debug" v-bind:style="svg_background"></div>
                <div class="subheader upcase">Title</div>
              </div>

            </div>

            <div class="back">

              <!-- inner back content -->
              <div class="back-inner">
                
                <!-- <div class="svg-icon-background svg-icon-background-small" v-bind:style="svg_background"></div> -->
                <div class="subheader upcase">Title</div>

                <p>
                  Ut gravida vel tortor eu maximus. Nunc facilisis et mi at laoreet. Quisque et luctus leo. Maecenas porta, nibh ac tristique aliquet, tellus leo vulputate risus, ut tempor nibh quam eu orci. Donec placerat condimentum nisi eget tincidunt. Nunc scelerisque ex eu lorem feugiat convallis. Curabitur eget nisl elit. Nullam sed imperdiet urna, non accumsan ligula. 
                </p>

                <!-- uncomment to test overflow -->
                <p>
                  In faucibus tellus nec dignissim tempor. Sed quis enim ut nunc pharetra convallis ac a magna. Sed velit mauris, malesuada vitae diam non, dictum tempus urna. Proin ac odio suscipit, malesuada sapien in, commodo sem. Quisque hendrerit libero pellentesque ex eleifend, eget eleifend elit viverra. Donec eget luctus turpis.
                </p>

              </div>

            </div>

            <div class="crosshair-container">
              <crosshair></crosshair>
            </div>

          </div>

        </div>

      </div>
    </div>

    <!-- messing with positioning/spacing -->
    
    <!-- <div class="row debug test-max-height">

      <div class="span-8-md debug test-align">

        <div class="ratio-image-slide debug test-align-inner">
          <h1>Image will go here</h1>
        </div>

      </div>

      <div class="span-8-md relative debug">

        <div class="ratio-square ind-2">
          <h1>Content</h1>
          <p>Lorem Ipsum</p>
        </div>

        <crosshair></crosshair>
        <span-background></span-background>

      </div>

    </div> -->

    <!-- nav -->
    <!-- <div class="nav-placeholder debug"></div> -->

  </div>
</template>

<script>

  import crosshair_background from '../components/atoms/crosshair_background'

  export default {
    data () {
      return {
        svg_background: {
          // 'position': 'relative',
          // 'height': '240px',
          // 'width': '240px',
          // 'background-color': 'blue'
        }
      }
    },
    components: {
      'crosshair': crosshair_background
    }
  }
</script>

<style scoped lang='scss'>

  @import '../styles/variables';

  // checkbox toggle
  .flip-indicator {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    opacity: 0;
    &:hover + .flipper,  &:checked + .flipper{
      opacity: 1;
      .back, .front {
        border: 1px solid $blue;
      }
    }
    &:hover + .flipper {
      .crosshair-container {
        opacity: 1;
      }
    }
    &:checked + .flipper {
      .back {
        transform: rotateY(0deg);
        max-height: 150vh; // expand back to fit content
      }
      .front {
        transform: rotateY(180deg);
      }
    }
  }

  .crosshair-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.75s;
  }

  //  vendor (see https://davidwalsh.name/css-flip#ieflip)

  // perspective container
  .flip-container {
    perspective: 1200px;
    transform-style: preserve-3d;
  }

  // grow container to dynamically sized back content (safe to assume front will usually be shorter)
  .flip-container, .back { 
    position: relative;
  }

  // position front absolute so relatively-positioned back can remain in correct place (top 0 left 0)
  .front {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    transform: rotateY(0deg);
  }

  .back {
    display: flex;
    max-height: 0px; // maintain 3-4 ratio when hidden
    transform: rotateY(-180deg);
    transition: max-height 1.5s;
  }

  .flip-container, .front, .back, .flipper {
    width: 100%;
    height: 100%;
  }
  
  /* flip speed goes here */
  .flipper {
    position: relative;
    // opacity: 0.8;
    transition: 0.75s;
    transform-style: preserve-3d;
  }

  /* hide back of pane during swap */
  .front, .back {
    border: 1px solid transparent;
    background-color: $white;
    backface-visibility: hidden;
    transition: 0.75s;
    transform-style: preserve-3d;
  }


  // inner content

  .front-inner, .back-inner {
    position: relative;
    margin: auto;
  }

  .front-inner {
    text-align: center;
  }

  .back-inner {
    padding: 24px;
  }

  .svg-icon-background {
    position: relative;
    width: 100px;
    height: 100px;
    margin-bottom: 24px;
  }

</style>


